<template>
    <li class="cart__item product">
        <div class="product__pic">
          <img :src="item.product.image" width="120" height="120" alt="item.product.title">
        </div>
        <h3 class="product__title">
          {{ item.product.title }}
        </h3>
        <span class="product__code">
          Артикул {{ item.product.id }}
        </span>

        <div class="product__counter form__counter">
          <button type="button" aria-label="Убрать один товар"  @click.prevent="decrement()">
            <svg width="10" height="10" fill="currentColor">
              <use xlink:href="#icon-minus"></use>
            </svg>
          </button>

          <input type="text" v-model.number="amount" name="count">

          <button type="button" aria-label="Добавить один товар"  @click.prevent="increment()">
            <svg width="10" height="10" fill="currentColor">
              <use xlink:href="#icon-plus"></use>
            </svg>
          </button>
        </div>
        <b class="product__price">
          {{ (item.amount * item.product.price) | numberFormat }} ₽
        </b>
        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteCart">
          <svg width="20" height="20" fill="currentColor">
            <use xlink:href="#icon-close"></use>
          </svg>
        </button>
    </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
    export default {
      data(){
        return {
            productLoading: false,
            productLoadingFaled: false,
            productDeleted: true,
            productDeletedSending: false
        }
      },
      filters: {
          numberFormat
      },
      props: ['item'],
      computed: {
          amount: {
            get(){
              return this.item.amount;
            },
            set(value){
              this.$store.dispatch('updateCartProductAmount',{productId: this.item.product.id, amount: value})
            },
          },
      },

      methods: {
        ...mapActions(['deleteProductFromCart']),
        deleteCart(){
          this.productDeleted = false,
          this.productDeletedSending = true;
          this.deleteProductFromCart({productId: this.item.product.id})
          .then(()=> {
            this.productDeleted = true;
            this.productDeletedSending = false;
          });
        },
     
        increment() {
          this.amount++;
        },

        decrement() {
          if (this.amount != 0){
            this.amount--;
          }
                
        },
      },
      watch: {
        handler() {
          this.deleteCart();
        },
      
      }
    }
</script>