<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"/>
            <BaseFormAdress title="Адрес доставки" v-model="formData.address" :error="formError.address" placeholder="Введите ваш адрес"/>
            <BaseFormPhone title="Номер телефона" v-model="formData.phone" :error="formError.phone" placeholder="Введите ваш телефон"/>
            <BaseFormEmail title="Email" v-model="formData.email" :error="formError.email" placeholder="Введите ваш Email"/>
            <BaseFormTextarea title="Комментарий к заказу" v-model="formData.comment" :error="formError.comment" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block" v-if="products">
          <ul class="cart__orders">
            <li class="cart__order" v-for="product in products" :key="product.id">
              <h3>{{ product.product.title }}</h3>
              <b> {{ (product.product.price * product.amount) | numberFormat }} ₽</b>
              <span>Артикул {{ product.productId }} </span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice | numberFormat}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div v-show="productAddSending">Товар отправляется........</div><div class="circle2" v-show="productAddSending"></div>
        <div class="cart__error form__error-block" v-if="formErorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import BaseFormAdress from '@/components/BaseFormAdress';
import BaseFormPhone from '@/components/BaseFormPhone';
import BaseFormEmail from '@/components/BaseFormEmail';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
    export default {
      filters: {numberFormat},
        components: {BaseFormText,BaseFormTextarea,BaseFormAdress,BaseFormPhone,BaseFormEmail},
    data() {
        return {
            formData: {},
            formError: {},
            formErorMessage: '',
            productAdded: false,
            productAddSending: false
        };
    },
    computed: {
      ...mapGetters({products: 'cartDetalProducts', totalPrice: 'cartTotalPrice'}),
    },
    methods: {
        order(){
            this.formError ={};
            this.formErorMessage = '';
            this.productAddSending = true;
            axios.post(API_BASE_URL + '/api/orders',{
                ...this.formData}, {
                    params: {
                        userAccessKey: this.$store.state.userAccessKey
                    }
            })
            .then((response) => {
                this.$store.commit('resetCart');
                this.$store.commit('updateOrderInfo', response.data);
                this.$router.push({name:'orderInfo', params: {id: response.data.id}});
                this.productAddSending = false;
            })
            .catch(error => {
                this.formError = error.response.data.error.request || {};
                this.formErorMessage = error.response.data.error.message;
                this.productAddSending = false;
            })
        }
    },
    watch: {
        handler() {
          this.order();
        },
    },
}

</script>